<template>
    <!--=== list feature ===-->
    <section class="feature list-feature" >
        <div class="container">
            <div class="row">
                <div class="col-12">
                    <div class="section-header">
                        <div class="sec-icon"><i class="ion ion-android-settings"></i></div>
                        <div class="sec-title">company information</div>
                    </div>
                </div>
<!-- 卡片 -->
                <div class="col-lg-6 col-sm-12" v-for="(item,index) in list" :key="index">
                    <div class="feature-wrap">
                        <div class="media">
                            <div class="align-self-center mr-4">
                                <div class="icon-wrap"><i class="ion ion-ios-navigate" ></i></div>
                            </div>
                            <div class="media-body">
                                <a href="javascript:;" @click="showMore(item)">{{item.title}}</a>
                                <p>{{item.content_descr}}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!--        详情弹窗-->
        <el-dialog title="" :visible.sync="dialog">
            <div v-html="more.content"></div>
        </el-dialog>
    </section>

</template>

<script>
    export default {
        name: "listfeature",
        data:()=>({
            list:[
                {
                    title:'Product Design',
                    content:'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna voluptate ut velit esse cillum.',
                    icon:'cube'
                },
                {
                    title:'Product Design',
                    content:'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna voluptate ut velit esse cillum.',
                    icon:'ios-navigate'
                },
                {
                    title:'Product Design',
                    content:'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna voluptate ut velit esse cillum.',
                    icon:'crop'
                },
                {
                    title:'Product Design',
                    content:'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna voluptate ut velit esse cillum.',
                    icon:'android-checkbox'
                }
            ],
            icon:['cube','ios-navigate','crop','android-checkbox'],
            more:{},
            dialog:false
        }),
        mounted(){
            this.getNews()
        },
        methods:{
            getNews(){
                let _self = this;
                _self.$ajax.post('/api/news',{})
                    .then(res=>{
                        _self.list = res.resultData
                })
            },
            showMore(item){
                this.dialog = !this.dialog
                this.more = item
            }
        }
    }
</script>

